.terminal {
    height: 500px;
    background-color: black;
    color: white;
    font-family: 'Courier New', Courier, monospace;
    font-weight:100;
    font-size:30px;
}

.terminal  input{
    background-color: transparent;
    border: none;
    font-size: 30px;
    color: white;
    field-sizing: content;
    padding: 0;
    caret-color: transparent;
    font-family: 'Courier New', Courier, monospace;
}

.terminal input:focus{
    outline: none;
}

.blinking-cursor {
    -webkit-animation: 1s blink step-end infinite;
    -moz-animation: 1s blink step-end infinite;
    -ms-animation: 1s blink step-end infinite;
    -o-animation: 1s blink step-end infinite;
    animation: 1s blink step-end infinite;
    margin-left: -6px;
  }
  
  @keyframes blink {
    from, to {
      color: transparent;
    }
    50% {
      color: white;
    }
  }
  
  @-moz-keyframes blink {
    from, to {
      color: transparent;
    }
    50% {
      color: white;
    }
  }
  
  @-webkit-keyframes blink {
    from, to {
      color: transparent;
    }
    50% {
      color: white;
    }
  }
  
  @-ms-keyframes blink {
    from, to {
      color: transparent;
    }
    50% {
      color: white;
    }
  }
  
  @-o-keyframes blink {
    from, to {
      color: transparent;
    }
    50% {
      color: white;
    }
  }